<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>添加账号</span>
      </div>

      <el-form :label-position="labelPosition" label-width="80px">
        <el-form-item label="用户:">
          <el-input class="userinput" v-model="acc" placeholder="请输入用户名"></el-input>
        </el-form-item>
        <el-form-item label="密码:">
          <el-input class="userinput" v-model="pwd" placeholder="请输入密码"></el-input>
        </el-form-item>
        <el-form-item label="用户组">
          <el-select v-model="userGroup" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <div class="addBtn">
            <el-button @click="clickAdd" type="primary">确定</el-button>
            <el-button @click="clickReset" type="info">重置</el-button>
          </div>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
// 引入api
import { API_ADD_ACCOUNT } from "@/API/apis.js";

export default {
  data() {
    return {
      acc: "", //账号
      pwd: "", //密码
      options: [
        //select选项
        {
          value: "超级管理员",
          label: "超级管理员"
        },
        {
          value: "普通管理员",
          label: "普通管理员"
        }
      ],
      userGroup: "", //select用户选择的数据
      labelPosition: "right", //表单label对齐方式
      nextClick: true //防抖节流
    };
  },
  methods: {
    clickAdd() {
      // 防抖节流判断
      if (this.nextClick == false) return;
      this.nextClick = false;
      // 发送axios请求
      API_ADD_ACCOUNT(this.acc, this.pwd, this.userGroup).then(res => {
        if (res.data.code == 0) {
          //返回码是0，添加成功
          this.$message({
            message: "添加用户成功",
            type: "success"
          });
          this.userGroup = "";
          this.acc = "";
          this.pwd = "";
        } else {
          this.$message.error("添加失败");
        }
        setTimeout(() => {
          this.nextClick = true; //三秒后把状态变为true，下次可以发送请求了
        }, 3000);
      });
    },
    clickReset() {
      if (this.nextClick == false) return;
      this.nextClick = false;
      this.userGroup = "";
      this.acc = "";
      this.pwd = "";
      this.$message("已重置输入信息");
      setTimeout(() => {
        this.nextClick = true; //三秒后把状态变为true，下次可以发送请求了
      }, 3000);
    }
  }
};
</script>

<style lang="less" scoped>
.userinput {
  width: 200px;
}
.addBtn {
  margin-top: 20px;
}
</style>